___CSS_LOADER_EXPORT___.push([module.id, `#root {
  background-color: #1e3a56;
}

.detail-container {
  width: 500px;
  height: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.navbar {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  padding: 10px;
  background-color: #1e3a56;
}

.back-button {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center;
  background-size: contain;
  border: none;
  width: 40px; /* 버튼 크기 조정 */
  height: 40px; /* 버튼 크기 조정 */
  cursor: pointer;
}

.back-button:hover {
  background-color: #000000;
}

.image-container {
  margin-top: 20px;
  width: 100px; /* 컨테이너 너비 */
  height: 450px;
  display: flex;
  justify-content: center; /* 가운데 정렬 */
}

.product-image {
  max-width: 100%; /* 이미지가 컨테이너를 넘지 않도록 */
  max-height: 150px; /* 이미지 최대 높이 지정 */
  object-fit: contain; /* 이미지 비율을 유지하며 크기 조정 */
}

.product-info {
  margin-top: 20px;
  text-align: center;
  color: #FFFFFF;
}

.tactile-info-container {
  margin-top: 20px;
}

.view-product-button {
  margin-top: 20px;
  background-color: #000000;
  color: #FFFFFF;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  width: 220px;
}

.view-product-button:hover {
  background-color: #FFFFFF;
  color: #000000;
}
